
        #splashscreen[data-visible] + #app {
          visibility: hidden;
          /*
          avoid big image to create a scroll
          or if the game is too big it will
          increase splashscreen size
          */
          width: 100%;
          height: 100%;
          overflow: hidden;
        }

        #splashscreen[data-visible] {
          display: flex;
        }

        #splashscreen {
          position: fixed;
          z-index: 1000;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          background: black;
          color: white;
          display: none;
          flex-direction: column;
          align-items: center;
        }

        #splashscreen #app-loading {
          display: flex;
          visibility: hidden;
          align-items: center;
          flex: 1;
        }
      